export default {
	data() {
		return {
			radioItems: [{
					name: '个人或事业单位',
					value: '2'
				},
				{
					name: '企业',
					value: '3'
				}
			],
			type: '3', // 发票类型 2个人 3企业
			name: '', // 抬头名称
			code: '', // 税号
			isShow: false,
			chequeLisit: []
		}
	},
	onLoad() {
		let invoice
		let pages = getCurrentPages()
		let pre = pages[pages.length - 2]
		if (pre != undefined) {





			invoice = pre.$vm.invoice






			if (invoice && invoice.hasOwnProperty('type') && invoice.type !== '1') {
				// 发票不是未使用, 展示发票信息
				this.name = invoice.name
				this.code = invoice.code
				this.type = invoice.type
			}
		}


	},
	methods: {
		// 单选框点击切换
		radioChange(evt) {
			this.radioItems.forEach(item => {
				if (item.value === evt.target.value) {
					this.type = item.value
				}
			})
		},
		// 不需要发票信息
		notNeedInvoice() {
			let data = {
				type: '1',
				name: '不开发票',
				code: ''
			}

			this.setPageData(data)
		},
		// 保存发票信息
		saveInvoice() {
			if (!this.name) {
				this.$common.errorToShow('请输入发票抬头')
				return false
			}

			// 企业类型需要输入税号
			if (this.type === '3' && !this.code) {
				this.$common.errorToShow('请输入发票税号信息')
				return false
			}

			let data = {
				type: this.type,
				name: this.name
			}

			// 不是企业类型不需要税号
			data['code'] = this.type === '3' ? this.code : ''

			this.setPageData(data)
		},
		// 向上个页面赋值并返回
		setPageData(data) {
			let pages = getCurrentPages(); //当前页
			let beforePage = pages[pages.length - 2]; //上个页面
			if (beforePage != undefined) {





				beforePage.$vm.invoice = data;






				uni.navigateBack({
					delta: 1
				})
			}

		},
		getCheque(event) {
			let name = event.detail.value
			if (name != '') {
				let data = {
					name: name
				}
				this.$api.getTaxInfo(data, res => {
					if (res.status) {
						if (res.data.length != 0) {
							this.isShow = true
							this.chequeLisit = res.data
						} else {
							// this.isShow = false
						}
					} else {
						this.$common.errorToShow(res.msg)
					}
				});
			} else {
				this.isShow = false
			}

		},
		chooseCheque(item) {
			this.name = item.name;
			this.code = item.code;
			this.isShow = false;
			this.type = '3';
		}
	}
}
